import React from "react";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";

//children - icon inside icon button (whatever is inside MyButton opening and closing tags)
//onClick- function
//tip - title of tooltip

export default ({ children, onClick, tip, btnClassName, tipClassName }) => (
  <Tooltip title={tip} placement="bottom" className={tipClassName}>
    <IconButton onClick={onClick} className={btnClassName} color="inherit">
      {children}
    </IconButton>
  </Tooltip>
);
